<template>
  <v-card flat>
    <v-toolbar flat color="primary" dark>
      <v-toolbar-title>Mission Microwave</v-toolbar-title>
    </v-toolbar>
      
    <v-form @submit.prevent="authenticateCredential">
      <v-row dense>
        <v-col> </v-col>
        <v-col>
          <v-text-field label="Username" required class="pt-md-8"
            v-model="userName" :rules="[IsValid(userName)]">
          </v-text-field>
        </v-col>
        <v-col> </v-col>
      </v-row>

      <v-row dense>
        <v-col> </v-col>
        <v-col>
          <v-text-field label="Password" required
            :type="passIsVisible ? 'text' : 'password'" :append-icon="passIsVisible ? 'mdi-eye' : 'mdi-eye-off'" @click:append="passIsVisible = !passIsVisible"
            v-model="password" :rules="[IsValid(password)]">
          </v-text-field>
        </v-col>
        <v-col> </v-col>
      </v-row>

      <v-row dense>
        <v-col> </v-col>
        <v-col>
          <v-btn :disabled="IsValid(userName) == false || IsValid(password) == false" type="submit">
            Login
          </v-btn>
        </v-col>
        <v-col> </v-col>
      </v-row>
    </v-form>

  </v-card>
</template>


<script>
import { mapActions } from 'vuex';

export default {
  name: 'Login',

  data: function() {
    return {
      passIsVisible : false,
      userName: "",
      password: "",
    }
  },

  methods: {
    ...mapActions(["PerformSetSecretKey"]),

    IsValid(input) {
      return input.length > 0;
    },

    authenticateCredential(event) {
      event.preventDefault();
      var userCredential = this.userName + "_" + this.password;
      sessionStorage.setItem('credential', userCredential);

      if (sessionStorage.getItem('credential') != null) {
        this.$router.push('/dashboard');
      }
    },

    authenticateLogin(event) {
      event.preventDefault();
      const url = 'http://192.168.235.56:28080/verify';
      var userCredential = this.userName + "_" + this.password;
      fetch(url, {
        method: 'POST',
        body: JSON.stringify({credential: userCredential})
      })
        .then(response => response.json())
        .then(data => {
          sessionStorage.setItem('credential', userCredential);
          this.$router.push('/dashboard');
          this.PerformSetSecretKey(data.response);
        });
    }
  }

}
</script>

